@use 'base/mixins'as m; //imm
@use 'base/variables'as v; //imv




.navegacion{
    background-color: v.$primario;
    padding: 2rem;
    margin: 2rem 0;

    &__border{
        border-top: 2px dashed v.$blanco;
        border-bottom: 2px dashed v.$blanco;
        padding: 1.7rem;
    }
    &__contenedor{
        display: flex;
        flex-direction: column;
        align-items: center;
        @include m.tablet { 
            flex-direction: row;
            justify-content: space-between;
            
        }
    }

    &__enlace{
        color: v.$blanco;
        font-size: 2.4rem;
        margin-bottom: 1.2rem;
        &:last-of-type{
            margin-bottom: 0;
        }

        @include m.tablet { 
           margin-bottom: 0;
        }
    }

}